html, body {
    height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 0;

    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.calendar-screen {
    height: 100vh;
    width: 100vw;
}

.calendar-container {
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
}

.calendar {
    margin-top: 5px;
    height: 85vh;
    width: 80vw;
}

/* Modal */
.ReactModalPortal > div{
    opacity: 0;
}

.ReactModalPortal .ReactModal__Overlay {
    align-items: center;
    display: flex;
    justify-content: center;
    transition: opacity .2s ease-in-out;
    z-index: 999;
}

.modal-fondo {
    background-color: rgba(0, 0, 0, 0.3);
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    position: fixed;
}

.ReactModalPortal .ReactModal__Overlay--after-open {
    opacity: 1;
}

.ReactModalPortal .ReactModal__Overlay--before-close {
    opacity: 0;
}

.modal {
    background: white;
    border-radius: 5px;
    color: rgb(51, 51, 51);
    display: inline;
    max-height: 620px;
    max-width: 500px;
    outline: none;
    padding: 10px;
}

/* Datepicker */
.react-datetime-picker input:focus{
    outline: none;
}

.react-datetime-picker__wrapper{
    display: flex;
    border: 0px solid gray !important;

}

/* FABS */
.fab {
    width: 70px; 
    height: 70px; 
    padding: 10px 16px; 
    border-radius: 35px; 
    font-size: 20px; 
    text-align: center; 
    bottom: 25px;
    position: fixed;
}

.fab.left {
    left: 15px;
}

.fab.right {
    right: 15px;
}